
import Swiper from 'vux/src/components/swiper/swiper.vue'

export default {
   data (){
      return {
         index: 0
      }
   },
   props: {
      list: {
         type: Array,
         default: [
            {
               url : 'javascript:;',
               img : require('../../assets/banner1.jpg')
            },
         ]
      },
      wpTitle: {
         type: String,
         default: '',
      }
   },
	components: {
      Swiper
	},
   methods: {
     change (index){
         this.index = index
     }
   }
}



